@import url(https://fonts.googleapis.com/css?family=Lora&display=swap);
.night-label{
	opacity: 0.2;
}


.layout
{
	width: 70vw;
	max-width: 300px;
	min-width: 250px;
	height: 30vh;
	display: grid;
	grid-template-columns: 1fr 2fr 1fr;
	justify-content: center;
	align-items: center;
	grid-gap: 30px;
	gap: 30px;
	font-size: 25px;
}

.layout:nth-child(1)
{
	text-align: right;
}

svg
{	
	width: 100%;
	height: 100%;
	cursor: pointer;
}

#toggle_night-content
{
	opacity: 0.5;
}

.inner-shadow
{
	stroke-opacity: 0.1;
	stroke-width: 5;
	stroke: black;
	fill: none;
}

.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.home_list {
    max-width: 80em;
    vertical-align: top;
    display: flex;
    flex-wrap: wrap;
    list-style-type: square;
    align-items: flex-start;
    align-content: flex-start;
    vertical-align: top;
}
/* .home_list li::before {
    display: inline-block;
