} */
.home_list li {
    min-width: 20em;
    line-height: 2rem;
}
.skill_tree {
    width: 100%;
    max-width: 60rem;
    display: flex;
    flex-wrap: wrap;
}
.skill_tree > li {
    flex: auto;
    margin: 0 0 1rem 0;
    min-width: 12em;
}
